import { mapValuesToThis } from '@jscrpt/common';
/**
 * Data block model for properties editor
 */

export class DataBlockModel {
  //######################### constructor #########################
  constructor(value) {
    //######################### public properties #########################

    /**
     * @inheritdoc
     */
    this.template = '';
    mapValuesToThis.bind(this)(value);
  }

}