import { codePropertiesControlFor } from '@anglr/dynamic/layout-editor';
import { HandlebarsLanguageModel } from '@anglr/dynamic';
import { DataBlockModel } from './dataBlock.model';
/**
 * Data block layout metadata
 */

export class DataBlockLayoutEditorMetadata {
  //######################### constructor #########################
  constructor() {
    //######################### public properties - implementation of LayoutEditorMetadataDescriptor #########################

    /**
     * @inheritdoc
     */
    this.metaInfo = {
      name: 'Data block',
      description: 'Data block - allows displaying data in template',
      group: 'Data',
      optionsMetadata: {
        propertiesMetadata: [{
          modelType: DataBlockModel,
          propertiesControls: [codePropertiesControlFor('template', HandlebarsLanguageModel)]
        }]
      }
    };
    Object.freeze(this);
  }

}